/* Ajout d'un style global */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
}

/* Barre de navigation */
.navbar {
    background-color: #333;
    padding: 15px;
}

.navbar-links {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.navbar-item {
    color: white;
    text-decoration: none;
    margin: 0 20px;
    font-size: 18px;
    transition: color 0.3s ease;
}

.navbar-item:hover {
    color: #ff6347; /* Rouge orangé au survol */
}

/* Ajout d'un style pour les pages */
.home-container, .login-container, .skills-container {
    text-align: center;
    padding: 50px;
    background-color: white;
    border-radius: 8px;
    margin: 20px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.home-container h1, .login-container h2, .skills-container h2 {
    color: #333;
}

.home-container p, .login-container p, .skills-container p {
    color: #555;
    font-size: 18px;
}
